<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="'互助日報表'" :routeTarget="{name: 'Report'}"></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">
      <div class="flex items-center gap-2">
        <p>日期：</p>
        <vc-date-picker v-model="searchDate" mode="date" :max-date="$dayjs().format('YYYY-MM-DD')">
          <template v-slot="{ inputValue, inputEvents }">
            <input
              class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
              :value="inputValue"
              v-on="inputEvents"
            />
          </template>
        </vc-date-picker>
      </div>

      <div class="text-left text-sm">
        <div class="my-4 flex items-center justify-between">
          <div class="flex items-baseline">
            <p class="mr-1 text-[24px] font-bold">{{$dayjs(searchDateSubmited).format('MM')}}月</p>
            <p>{{$dayjs(searchDateSubmited).format('DD')}}日</p>
          </div>
          <div>
            <p>設計師 {{userInfo.user.name}}</p>
          </div>
        </div>
        <div class="break-words" v-if="reviewResult && reviewResultWholeMonth">
          <div class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 bg-[#08424c]/80 p-1 text-white ">
            <div class="w-1/12">#</div>
            <div class="w-4/12">項目</div>
            <div class="w-3/12 text-right">設計師</div>
            <div class="w-2/12 text-right">金額</div>
            <div class="w-2/12 text-right">點</div>
          </div>
          <div v-for="(helperRecordItem, index) in showFee" :key="`helperRecordItem${index}`" class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 p-1 ">
            <div class="w-1/12">#{{helperRecordItem.orderNo}}</div>
            <div class="w-4/12">{{helperRecordItem.service.name}}</div>
            <div class="w-3/12 text-right">{{helperRecordItem.designer.name}}</div>
            <!-- 金額 -->
            <div class="w-2/12 text-right">{{helperRecordItem.helperFee}}</div>
            <div class="w-2/12 text-right">{{helperRecordItem.helperPoint}}</div>
          </div>
          <div v-for="(productRecordItem, index) in reviewResult.productRecord" :key="`productRecordItem${index}`" class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 p-1 ">
            <div class="w-1/12">#{{productRecordItem.orderNo}}</div>
            <div class="w-4/12">
              {{ productRecordItem.productName ? productRecordItem.productName + ' + ': ''}}
              {{ productRecordItem.productUnitPrice ? productRecordItem.productUnitPrice + ' x ': ''}}
              {{ productRecordItem.productCount }}
            </div>
            <div class="w-3/12 text-right"></div>
            <div class="w-2/12 text-right">{{productRecordItem.productUnitPrice*productRecordItem.productCount}}</div>
            <div class="w-2/12 text-right"></div>
          </div>
          <div class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 p-1 ">
            <div class="w-2/12">合計</div>
            <!-- 金額 v-if="priceDisplayOnDailyReportDefault.isPriceDisplayOnDailyReport"-->
            <div class="w-8/12 text-right"  v-to-fixed>{{isUsingMathRoundInDesignerDailyReport ? Math.round(totalCount): totalCount}}</div>
            <!-- 點 -->
            <div class="w-2/12 text-right" v-to-fixed>{{isUsingMathRoundInDesignerDailyReport ? Math.round(totalCountPoint): totalCountPoint}}</div>
          </div>
        </div>
        <div class="break-words" v-if="reviewResult && reviewResultWholeMonth">
          <div class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 bg-[#08424c]/80 p-1 text-white ">
            <div class="w-2/5"></div>
            <div class="w-2/5 text-right">日合計</div>
            <div class="w-1/5 text-right">累計</div>
          </div>
          <div class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50" v-if="isDisplayHelperRetrieveInDailyReport && !(reviewResult.summaryNoPoint === 0)">
            <div class="w-2/5">互助獲得金額(不含點)</div>
            <div class="w-2/5 text-right" v-to-fixed>{{isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResult.summaryNoPoint) :reviewResult.summaryNoPoint }}</div>
            <div class="w-2/5 text-right" v-to-fixed>{{isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResultWholeMonth.summaryNoPoint) :reviewResultWholeMonth.summaryNoPoint }}</div>
          </div>
          <div class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50" v-if="isDisplayHelperRetrieveInDailyReport && !( reviewResult.pointSummary === 0)">
            <div class="w-2/5">互助獲得(點)點數/換算金額</div>
            <div class="w-2/5 text-right">
              <span v-to-fixed>{{ isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResult.pointSummary): reviewResult.pointSummary }}</span>
              <template v-if="priceDisplayOnDailyReportDefault.isHelperPointPriceDisplay">
                / <span v-to-fixed>{{isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResultWholeMonth.summaryOnlyPoint*-1) : reviewResultWholeMonth.summaryOnlyPoint*-1 }}</span>
              </template>
            </div>
            <div class="w-1/5 text-right" >
              <span v-to-fixed>{{isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResult.pointSummary): reviewResult.pointSummary }}</span>
              <template v-if="priceDisplayOnDailyReportDefault.isHelperPointPriceDisplay">
                / <span v-to-fixed>{{isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResultWholeMonth.summaryOnlyPoint*-1): reviewResultWholeMonth.summaryOnlyPoint*-1 }}</span>
              </template>
            </div>
          </div>
          <div class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50">
            <div class="w-2/5">店販業績</div>
            <div class="w-2/5 text-right" v-to-fixed>{{ isUsingMathRoundInDesignerDailyReport ? tst(reviewResult.productPerformanceSummary) : reviewResult.productPerformanceSummary }}</div>
            <div class="w-1/5 text-right" v-to-fixed>{{ isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResultWholeMonth.productPerformanceSummary) : reviewResultWholeMonth.productPerformanceSummary }}</div>
          </div>
          <div class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50">
            <div class="w-2/5">店販抽成</div>
            <div class="w-2/5 text-right" v-to-fixed>{{ isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResult.productRakeSummary) : reviewResult.productRakeSummary}}</div>
            <div class="w-1/5 text-right" v-to-fixed>{{ isUsingMathRoundInDesignerDailyReport ? Math.round(reviewResultWholeMonth.productRakeSummary) : reviewResultWholeMonth.productRakeSummary}}</div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'priceList-index',
  description: '報表的互助日報表',
  metaInfo: {
    title: 'BeautyOS 設計師',
    titleTemplate: '%s | 互助日報表',
  },
  data() {
    return {
      searchDate: this.$dayjs().valueOf(),
      searchDateSubmited: this.$dayjs().valueOf(),
      reviewResult: null,
      reviewResultWholeMonth: null,
      isDisplayHelperRetrieveInDailyReport: false,
      isUsingMathRoundInDesignerDailyReport: false,
      isZeroValueHiddenOnDailyReport: false,
      priceDisplayOnDailyReportDefault: {},
      isRefresh: true,
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    tst(param) {
      return Math.round(param);
    },
    async init() {
      await Promise.all([this.getIsDisplayHelperRetrieveInDailyReport(), this.getIsUsingMathRoundInDesignerDailyReport(), this.getIsZeroValueHiddenOnDailyReport(), this.getPriceDisplayOnDailyReportDefault()]).then(() => {});
      await this.getUserHelperRetrieve();
      await this.getUserHelperRetrieveWholeMonth();
    },
    async getIsDisplayHelperRetrieveInDailyReport() {
      const resp = await this.$api.getIsDisplayHelperRetrieveInDailyReport();
      const { data, errors } = resp.data;
      if (errors) return;
      this.isDisplayHelperRetrieveInDailyReport = data.getIsDisplayHelperRetrieveInDailyReport;
    },
    async getIsUsingMathRoundInDesignerDailyReport() {
      const resp = await this.$api.getIsUsingMathRoundInDesignerDailyReport();
      const { data, errors } = resp.data;
      if (errors) return;
      this.isUsingMathRoundInDesignerDailyReport = data.getIsUsingMathRoundInDesignerDailyReport;
    },
    async getIsZeroValueHiddenOnDailyReport() {
      const resp = await this.$api.getIsZeroValueHiddenOnDailyReport();
      const { data, errors } = resp.data;
      if (errors) return;
      this.isZeroValueHiddenOnDailyReport = data.getIsZeroValueHiddenOnDailyReport;
    },
    // 金額判斷依據
    async getPriceDisplayOnDailyReportDefault() {
      const resp = await this.$api.getPriceDisplayOnDailyReportDefault();
      const { data, errors } = resp.data;
      if (errors) return;
      this.priceDisplayOnDailyReportDefault = data.getPriceDisplayOnDailyReportDefault;
    },
    doRoute() {
      this.$router.push({ name: 'Punch' });
    },
    // 取得互助日資料
    async getUserHelperRetrieve() {
      this.reviewResult = null;
      this.searchDateSubmited = this.searchDate;
      const submitForm = {
        designerId: this.userInfo.user.id,
        startDate: this.$dayjs(this.$dayjs(this.searchDate).format('YYYY-MM-DD 00:00:00')).valueOf(),
        endDate: this.$dayjs(this.$dayjs(this.searchDate).format('YYYY-MM-DD 23:59:59')).valueOf(),
      };
      const resp = await this.$api.getUserHelperRetrieve(submitForm);
      const { data, errors } = JSON.parse(JSON.stringify(resp.data));
      if (errors) return;
      this.reviewResult = data.getUserHelperRetrieve;
    },
    // 取得整個月的互助日資料
    async getUserHelperRetrieveWholeMonth() {
      this.reviewResultWholeMonth = null;
      this.searchDateSubmited = this.searchDate;
      const submitForm = {
        designerId: this.userInfo.user.id,
        startDate: this.$dayjs(this.$dayjs(this.$dayjs(this.searchDate).startOf('month')).format('YYYY-MM-DD 00:00:00')).valueOf(),
        endDate: this.$dayjs(this.$dayjs(this.$dayjs(this.searchDate).endOf('month')).format('YYYY-MM-DD 23:59:59')).valueOf(),
      };
      const resp = await this.$api.getUserHelperRetrieve(submitForm);
      const { data, errors } = JSON.parse(JSON.stringify(resp.data));
      if (errors) return;
      this.reviewResultWholeMonth = data.getUserHelperRetrieve;
    },
    reducerCounter(NumList) {
      return NumList.reduce((accumulator, curr) => +accumulator + +curr);
    },
  },
  watch: {
    searchDate() {
      if (!this.searchDate) return;
      this.getUserHelperRetrieve();
      this.getUserHelperRetrieveWholeMonth();
    },

  },
  computed: {
    ...mapState('userInfo', ['userInfo']),
    showFee() {
      const newArray = this.reviewResult.helperRecord.map((item) => {
        const newObj = item;
        if (this.priceDisplayOnDailyReportDefault.isHelperPointPriceDisplay) {
          newObj.helperFee = -(item.helperFeeOnlyPoint + item.helperFeeNoPoint);
          return newObj;
        }
        if (item.helperMoneyGivenWay === '點數換算金額') {
          newObj.helperFee = '';
          return newObj;
        }
        newObj.helperFee = -item.helperFeeNoPoint;
        return newObj;
      });
      return newArray;
    },
    // 點數總計
    totalCountPoint() {
      let count = 0;
      if (Array.isArray(this.reviewResult.helperRecord) && this.reviewResult.helperRecord.length > 0) {
        const priceList = this.reviewResult.helperRecord.map((item) => item.helperPoint);
        count = this.reducerCounter(priceList);
      }
      return count;
    },
    // 總計
    totalCount() {
      return this.showFee.reduce((pre, nex) => ({ helperFee: +pre.helperFee + +nex.helperFee }), { helperFee: 0 }).helperFee;
      // return this.totalCountHelperRecord + this.totalCountLProductRecord;
    },
    totalCountHelperRecord() {
      let count = 0;
      if (Array.isArray(this.reviewResult.helperRecord) && this.reviewResult.helperRecord.length > 0) {
        const priceList = this.reviewResult.helperRecord.map((item) => item.helperFee);
        count = this.reducerCounter(priceList);
      }
      return count;
    },
    totalCountLProductRecord() {
      let count = 0;
      if (Array.isArray(this.reviewResult.productRecord) && this.reviewResult.productRecord.length > 0) {
        const priceList = this.reviewResult.productRecord.map((item) => item.productUnitPrice * item.productCount);
        count = this.reducerCounter(priceList);
      }
      return count;
    },
  },
};
</script>

<style>
